.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.block {
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
  justify-content: center;
}
.logout_button {
  margin: 0;
  border-radius: 10px;
  width: 80px;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.logout_button:hover {
  background-color: blue;
  color: white;
}
